import React, { Component, useState, useEffect } from 'react';
import "./Poll.css"
import {v4 as uuid} from 'uuid'
import {useCookies} from 'react-cookie';
import PollContent from './PollContent';


const contents = [
    {
        awardName:'능지에 치명상',
        content:[
            {
                name:'HelpBee',
                index: 0,
                image: "logo512.png"
            },
            {
                name:'StockMarket',
                index: 1,
                image: "logo512.png"
            },
            {
                name: "Arksung's Epidemic",
                index: 2,
                image: "logo512.png"
            },
            {
                name: "Arksung's Epidemic",
                index: 2,
                image: "logo512.png"
            }
        ]
    },
    {
        awardName:'시간순상',
        content:[
            {
                name:'HelpBee',
                index: 0,
                image: "logo512.png"
            },
            {
                name:'StockMarket',
                index: 1,
                image: "logo512.png"
            },
            {
                name: "Arksung's Epidemic",
                index: 2,
                image: "logo512.png"
            }
        ]
    },
]




function Poll({
    content
})
{

    const [id, setID] = useState("");

    const [cookies, setCookie, removeCookie] = useCookies(['uniqueId']);

    useEffect(()=>{        

        if(cookies.uniqueId !== undefined)
        {
            setID(cookies.uniqueId);
        }
        else{
            const newId = uuid();
            setID(newId);
            setCookie('uniqueId', newId);
        }
    })

    console.log(contents);

    return(
        <div className="Poll">
            {contents.map((content, index) => (
                <div className="PollContainer">

                    <PollContent
                        key={index}
                        awardName={content.awardName}
                        sheetNum={index + 1}
                        id={id}
                        contents={content.content}
                    >
                    </PollContent>

                </div>
             ))}  
        </div>
    )
}




export default Poll;