import React, { Component, useState, useEffect } from 'react';
import './PollContent.css'
import $ from "jquery";
import VoteResult from './VoteResult'
import Carousel from 'react-multi-carousel'
import 'react-multi-carousel/lib/styles.css';

let list = [
    {
        name: ['helpbee', 'stockmarket', 'arksung']
    },
    {
        name: ['helpbee', 'stockmarket', 'arksung']
    }
]

const responsive = {
    superLargeDesktop: {
      // the naming can be any, depends on you.
      breakpoint: { max: 4000, min: 3000 },
      items: 5,
      partialVisibilityGutter: 100
    },
    desktop: {
      breakpoint: { max: 3000, min: 900 },
      items: 3,
      partialVisibilityGutter: 100
    },
    tablet: {
      breakpoint: { max: 850, min: 600 },
      items: 2,
      partialVisibilityGutter: 100
    },
    mobile: {
      breakpoint: { max: 600, min: 0 },
      items: 1,
      partialVisibilityGutter: 100
    }
  };


class PollContent extends React.Component{

    constructor(props){
        super(props);
        this.state = {
            awardName: '',
            sheetNum: 0,
            showVote: true,
            id: '',
            contents: null,
            count: 0,
            half: 0,
            breakPoints: [
                { width: 400, itemsToShow: 1 },
                { width: 600, itemsToShow: 2 },
                { width: 900, itemsToShow: 5 }
            ],
        } 
    }

    componentDidMount = () => {

        this.setState({
            count: this.props.contents.length,
            half: this.props.count / 2,
        })

        
    };

    voteGame = (id, index)=> {

        console.log(this.props.sheetNum);

        $.ajax({
            type: "GET",
            url: `https://script.google.com/macros/s/AKfycbwVuwa_4i617rg-Z8TssavFxz18_TGvCzV8h0g0j5lq1ACC3A8CP7gQCuErQnsbos_W/exec?sheetName=시트${this.props.sheetNum}`,
            data: {
              "post_ID": id,
              "choice": index
            },
            success: function(response){
                alert("투표 완료");
            }
        });
    }

    setVoteResult = () => {
        const SOURCE = `https://spreadsheets.google.com/feeds/list/1rl0fCsvCnAg9uWTKzztFlFd3Q2E08-zjRAeCGCd-55s/${this.props.sheetNum}/public/full?alt=json`;
        const datas = new Array();
        const labels = new Array();
        var sum = 0;

        fetch(SOURCE)
        .then(res => res.json())
        .then(
            (result) => {
                const Content = this.props.contents;

                for(var i = 0; i < Content.length; ++i)
                {
                    datas.push(result['feed']['entry'][0]['gsx$' + `${list[this.state.sheetNum].name[i]}`]['$t']);
                    labels.push(Content[i].name);
                }

                console.log(labels);
                console.log(datas);

                this.setState({
                    test:
                        <VoteResult
                            data={{
                                labels: labels,
                                datasets:[{
                                    label:'투표 수',
                                    data: datas,
                                }]
                            }}
                        >
                        </VoteResult>
                })
            },
            // 주의: 컴포넌트의 실제 버그에서 발생하는 예외사항들을 넘기지 않도록 
            // 에러를 catch() 블록(block)에서 처리하기보다는 
            // 이 부분에서 처리하는 것이 중요합니다.
            (error) => {
                    
            }
        )
    }

    showVoteResult = () =>{

        this.setVoteResult();

        this.setState({
            showVote: !this.state.showVote,
            
        })
    }

    render()
    {

        return(
            <React.Fragment>

                <div className='Info'>
                    <a className="AwardName">{this.props.awardName}</a>
                    <button onClick={() => this.showVoteResult()} className='PollState'>투표 현황</button>
                </div>

                <div className='voteSelect' style={this.state.showVote === true ?  {display:"flex"}: {display: 'none'}}>
                    <Carousel centerMode={true} itemClass='carousel-item' containerClass='carousel-container' responsive={responsive} >
                        {this.props.contents.map((content, index) => (
                            <div className="PollContent" key={index}>
                                <div>
                                    <a>{content.name}</a>
                                    <img src={content.image}></img>
                                    <button onClick={() => this.voteGame(this.props.id, content.index)}>선택하기</button>
                                </div>
                            </div>
                        ))}
                    </Carousel>
                </div>
                
                
                <div className="VoteResult" style={this.state.showVote === true ?  {display:"none"}: {display: 'flex'}}>
                    {this.state.test}
                </div>
            </React.Fragment>
        )
    }
}

export default PollContent;